body {
    background-color: #43314d;
    padding: 20px;
    margin: 20;
  }
  h1, h2, p, ul, li {
    font-family: monospace;
  }
  ul.header li {
    display: inline;
    list-style-type: none;
    margin: 0;
  }
  ul.header {
    background-color: #805D93;
    padding: 0;
  }
  ul.header li a {
    color: #FFF;
    font-weight: bold;
    text-decoration: none;
    padding: 20px;
    display: inline-block;
  }
  .content {
    background-color: #533c5f;
    justify-content: center;
    align-items: center;
    padding: 20px;
  }
  .content h2 {
    padding: 0;
    margin: 0;
  }
  .content li {
    margin-bottom: 10px;
    
  }

  .container-div{ 
    Display: flex;
    Height: 100%;
    Width: 100%; 
    align-content: center;
    justify-content: center;
    }

    .g-card {
        margin: 20px;
      }
      
      
      .g-card-image {
        border-radius: 10px;
        width: 300px;
        height: 480px;
        box-shadow: 0px 0px 3px 1px #ccc;
      }
      
      
      .g-card-info {
        margin-top: 10px;
        min-height: 100px;
      }
      
      
      .g-card-title {
        font-size: 24px;
        margin: 0px;
      }
      
      
      .g-card-sub-title {
        font-size: 16px;
        margin: 0px;
      }
      
      
      .success-msg {
        color: #57A773;
        padding: 10px 15px;
      }
      
      
      .err-msg {
        color: #EE6352;
        padding: 10px 15px;
      }

